// src/components/ProjectsSection.tsx
'use client';
import React, { useRef, useMemo } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { gsap } from 'gsap'; // Import GSAP for the flowing animation
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowUpRightFromSquare, faCode } from '@fortawesome/free-solid-svg-icons';

// --- DATA STRUCTURE ---
interface Project {
  title: string;
  description: string;
  image: string;
  tags: string[];
  githubUrl: string;
  liveUrl: string;
}

// Sample Project Data
const PROJECTS: Project[] = [
  {
    title: "AI Recipe Generator",
    description: "A full-stack application leveraging OpenAI's API to generate unique recipes based on user-provided ingredients and dietary preferences.",
    image: "/images/ai-recipe.jpg", 
    tags: ["Next.js", "TypeScript", "Tailwind CSS", "OpenAI API", "MongoDB"],
    githubUrl: "https://github.com/yourusername/ai-recipe-generator",
    liveUrl: "https://ai-recipe.vercel.app/",
  },
  {
    title: "E-commerce Backend Service",
    description: "A robust RESTful API built with Node.js and Express, handling user authentication, order processing, and inventory management.",
    image: "/images/ecommerce-api.jpg", 
    tags: ["Node.js", "Express", "PostgreSQL", "Prisma", "AWS EC2"],
    githubUrl: "https://github.com/yourusername/ecommerce-backend",
    liveUrl: "#", 
  },
  {
    title: "Custom OS Monitoring Tool",
    description: "A desktop application written in Python that monitors system resources (CPU, Memory, Disk) and logs performance metrics locally.",
    image: "/images/os-monitor.jpg", 
    tags: ["Python", "Tkinter", "System Metrics", "Data Logging"],
    githubUrl: "https://github.com/yourusername/os-monitor-tool",
    liveUrl: "#", 
  },
];

// --- PROJECT MENU ITEM COMPONENT (The Flowing Menu Logic) ---
const ProjectMenuItem: React.FC<{ project: Project }> = ({ project }) => {
  const itemRef = useRef<HTMLDivElement>(null);
  const marqueeRef = useRef<HTMLDivElement>(null);
  const marqueeInnerRef = useRef<HTMLDivElement>(null);

  const animationDefaults = { duration: 0.6, ease: 'expo.out' };

  const findClosestEdge = (mouseX: number, mouseY: number, width: number, height: number): 'top' | 'bottom' => {
    const topEdgeDist = Math.pow(mouseX - width / 2, 2) + Math.pow(mouseY, 2);
    const bottomEdgeDist = Math.pow(mouseX - width / 2, 2) + Math.pow(mouseY - height, 2);
    return topEdgeDist < bottomEdgeDist ? 'top' : 'bottom';
  };

  const handleMouseEnter = (ev: React.MouseEvent<HTMLAnchorElement>) => {
    if (!itemRef.current || !marqueeRef.current || !marqueeInnerRef.current) return;
    const rect = itemRef.current.getBoundingClientRect();
    const edge = findClosestEdge(ev.clientX - rect.left, ev.clientY - rect.top, rect.width, rect.height);

    const tl = gsap.timeline({ defaults: animationDefaults });
    tl.set(marqueeRef.current, { y: edge === 'top' ? '-101%' : '101%' })
      .set(marqueeInnerRef.current, { y: edge === 'top' ? '101%' : '-101%' })
      .to([marqueeRef.current, marqueeInnerRef.current], { y: '0%' });
  };

  const handleMouseLeave = (ev: React.MouseEvent<HTMLAnchorElement>) => {
    if (!itemRef.current || !marqueeRef.current || !marqueeInnerRef.current) return;
    const rect = itemRef.current.getBoundingClientRect();
    const edge = findClosestEdge(ev.clientX - rect.left, ev.clientY - rect.top, rect.width, rect.height);

    const tl = gsap.timeline({ defaults: animationDefaults }); 
    tl.to(marqueeRef.current, { y: edge === 'top' ? '-101%' : '101%' })
      .to(marqueeInnerRef.current, {
        y: edge === 'top' ? '101%' : '-101%'
      }, 0); 
  };

  const repeatedMarqueeContent = useMemo(() => {
    return Array.from({ length: 4 }).map((_, idx) => (
      <div key={idx} className="flex items-center space-x-8 px-8">
        {/* Project Image */}
        <div className="relative w-[150px] h-[7vh] rounded-lg overflow-hidden flex-shrink-0">
          <Image
            src={project.image}
            alt={project.title}
            fill
            className="object-cover"
            sizes="150px"
            priority={true}
          />
        </div>
        
        {/* Project Title and Tags */}
        <div className="flex flex-col flex-shrink-0 min-w-[300px]">
          <span className="text-[#060010] uppercase font-bold text-[3vh] leading-none">
            {project.title}
          </span>
          <div className="flex flex-wrap gap-2 mt-1">
            {project.tags.slice(0, 3).map((tag, i) => ( 
              <span 
                key={i} 
                className="px-2 text-xs font-medium text-white bg-blue-600 rounded"
              >
                {tag}
              </span>
            ))}
          </div>
        </div>
      </div>
    ));
  }, [project]);

  return (
    <div className="flex-1 relative overflow-hidden text-center shadow-[0_-1px_0_0_#fff]" ref={itemRef}>
      <a
        className="flex items-center justify-between h-full relative cursor-pointer uppercase no-underline font-semibold text-white text-[4vh] p-8 transition-colors duration-300 hover:text-[#060010] focus:text-white focus-visible:text-[#060010]"
        href={project.liveUrl !== '#' ? project.liveUrl : project.githubUrl}
        target="_blank"
        rel="noopener noreferrer"
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
      >
        <span>{project.title}</span>
        
        {/* Project Links (Visible when not hovered) */}
        <div className="flex gap-4 text-white text-base font-normal normal-case transition-opacity duration-300 group-hover:opacity-0">
            {project.githubUrl && (
              <span className="flex items-center gap-2 hover:text-blue-400">
                <FontAwesomeIcon icon={faCode} className="w-5 h-5" />
                Code
              </span>
            )}
            {project.liveUrl !== '#' && (
              <span className="flex items-center gap-2 hover:text-blue-400">
                <FontAwesomeIcon icon={faArrowUpRightFromSquare} className="w-5 h-5" />
                Live
              </span>
            )}
        </div>
      </a>
      
      {/* MARQUEE OVERLAY */}
      <div
        className="absolute top-0 left-0 w-full h-full overflow-hidden pointer-events-none bg-white translate-y-[101%]"
        ref={marqueeRef}
      >
        <div className="h-full w-[200%] flex" ref={marqueeInnerRef}>
          <div className="flex items-center relative h-full w-[200%] will-change-transform animate-marquee">
            {repeatedMarqueeContent}
          </div>
        </div>
      </div>
    </div>
  );
};

// --- MAIN SECTION COMPONENT ---
const ProjectsSection: React.FC = () => {
  return (
    <section id="projects" className="py-20 md:py-32 bg-gray-50">
      <div className="max-w-5xl mx-auto px-6 lg:px-8">
        
        {/* Section Heading */}
        <h2 className="text-4xl md:text-5xl font-extrabold text-gray-800 text-center mb-4">
          My Projects
        </h2>
        <p className="text-xl text-gray-600 text-center mb-16 max-w-2xl mx-auto">
          Hover over each project for a dynamic preview of the tech stack and visual concept.
        </p>

        {/* Menu Container: Must define a specific height for the menu items to flex */}
        <div className="border border-gray-300 rounded-xl overflow-hidden shadow-2xl" style={{ height: '400px' }}>
          <nav className="flex flex-col h-full m-0 p-0 bg-[#060010]">
            {PROJECTS.map((project, idx) => (
              <ProjectMenuItem key={idx} project={project} />
            ))}
          </nav>
        </div>

      </div>
    </section>
  );
};

export default ProjectsSection;